.light-mode {
  background-color: white;
}

.dark-mode{
  background-color: #333333;
}

img{
  width: 100%;
  height: auto;
}

body {
  margin: 0;
  background-color: black;
  padding-left: 25%;
  padding-right: 25%;
}

body,
td,
th,
tr,
p,
ul,
li,
a,
.content,
#content,
.header {
    font-family:'Amiri-Regular', 'Source Sans Pro', 'Open Sans','Lato','Helvetica';
    font-size: 25px;
    font-weight:100;
    color: white;
}

a {
  text-decoration: none;
  background-image: linear-gradient(120deg, #d1aaf0  0%, #00b9d8 100%);
  background-repeat: no-repeat;
  background-size: 200% 0.2em;
  background-position: 0 90%;
  transition: background-size 0.25s ease-in;
}

/* a:focus, */
a:hover {
  background-size: 100% 88%;
}

ul.header li a:focus,
ul.header li a:hover{
  /* color: black; */
  text-decoration: none;
}

.special{
  font-family: "Angelwine";
  font-size: 90px;
  /* color: #c6dee2; */
}

.gif{
  height: 60vh;
  width: auto;
}

.homebutton{
  align-content: center;
  text-align: center;
}

h1{
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-family: 'Angelwine', 'Kalam', 'Source Sans Pro','Helvetica', 'Lato', 'Open Sans';
  font-size: 120px;
  text-align: center;
  color: white;
}

h2 {
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-size: 50px;
  color: white;
}

h3,
h3 span,
h3 a{
  font-family: 'Amiri-Bold',  'Amiri-Regular', 'Source Sans Pro', 'Courier','Open Sans','Lato','Helvetica';
  /* font-family: 'Open Sans', 'Trebuchet MS', 'Roboto','Lato', Verdana, Helvetica, sans-serif; */
  font-size: 30px;
  font-weight: bolder;
  color: white;
}

#language {
  font-weight: bold;
  font-family: 'Amiri-Bold';
}

@media (max-width: 700px) and (min-width: 480px) {

  body {
    margin: 0;
    background-color: black;
    padding-left: 15%;
    padding-right: 15%;
  }

  h1{
    font-size: 65px;
  }

  h2{
    font-size: 25px;
  }

  h3,
  h3 span,
  h3 a{
    font-size: 22px;
  }

  body,
  tr,
  p,
  a, 
  li {
      font-size: 20px;
  }
  
  strong {
      font-size: 20px;
  }


  ul.header li {
    display: inline;
    list-style-type: none;
  }
  
  ul.header {
    font-weight:100;
  }
  
  ul.header li a {
    color: black;
    /* display: inline-block; */
  }
  

}

@media (max-width: 480px) {

  body {
    margin: 0;
    background-color: black;
    padding-left: 15%;
    padding-right: 15%;
  }

  h1{
    font-size: 55px;
  }

  h2{
    font-size: 30px;
  }

  body,
  h3,
  h3 span,
  h3 a,
  tr,
  p,
  a,
  li {
      font-size: 20px;
  }

  ul a, ul,  ul{
    padding-inline-start: 0;
  
  }
  .special{
    font-size: 55px;
  }

  .main ul{
    padding-inline-start: 0%;
  }
  
  strong {
      font-size: 17px;
      /* font-weight: 400; */

  }
/* 
  #header{
    width: 95%;
